.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.vidbox {
  margin: 15px 10px;
  transition: all 150 ms;
}

.vidbox:hover{
  transform: scale(1.47989)
}

.vidContainer {
  display: grid;
  grid-template-columns: 303px 303px 303px;
  justify-content: center;
}

@media only screen and (max-width:768px){
  .vidContainer{
    display: grid;
    grid-template-columns: 303px;
    justify-content: center;
  }
  .vidbox:hover{
    transform: none;
  }
}

.ads{
  position: absolute;
  top: 95px;
  width:160px;
  height:600px;
}

#ad1 {
  left: 60px;
}

#ad2 {
  right: 60px;
}
